    <span v-if="Registered">
      <!-- IF spelaren Ã¤r inloggad sÃ¥ visas denna komponent nere i hÃ¶ger hÃ¶rn pÃ¥ vÃ¥r apps alla sidor. -->
      <h1 id="playerScore">
        {{ localAlias }}
        {{ localScore }}
      </h1>
    </span>
    <span v-else-if="Temporary">
      <h1 id="playerScore">
        {{ localScore }}
      </h1>
