        this.localScore = localStorage.getItem('localScore')
        this.Registered = localStorage.getItem('Registered')
        this.Temporary = localStorage.getItem('Temporary')
        this.$forceUpdate() // DÃ¥ komponenten skapas hÃ¤mtar den data frÃ¥n Localstorage, och fÃ¶r att bli nÃ¥got sÃ¥nÃ¤r responsiv sÃ¥ uppdateras den varje sekund och kollar i lokalstorage igen.
