<template>
  <span id="winner">
    <h1>GOOD JOB! You earned 100 points!</h1>
    <button id="myBtn" @click="increaseAndClose">
      Collect Points
    </button>
  </span>
</template>

<script>
  import { mapMutations, mapActions } from 'vuex' // HÃ¤r importerar jag funktioner frÃ¥n tvÃ¥ olika moduler i Vuex, en specifik modul fÃ¶r spelet, en frÃ¥n modulen som hÃ¥ller spelardata, som namn och poÃ¤ng.

  export default {
    data() {
      return {
        current: null,
        toNum: null
      }
    },
    methods: {
      ...mapMutations('playerData', {
        increaseScore: 'closeCollect'
      }),
      ...mapActions('d', {
        startNewGame: 'startNewGame'
      }),
      increaseAndClose() {
        localStorage.setItem('Temporary', 'true')
        if (localStorage.getItem('localScore')) {
          // Om spelaren har ett score i Localstorage sÃ¥ adderas poÃ¤ngen till den.
          this.current = localStorage.getItem('localScore')
          this.toNum = parseInt(this.current) // I localstorage sparas datan som strÃ¤ng, hÃ¤r gÃ¶r jag om den till number
        } else {
          this.toNum = 0 // Finns inget vÃ¤rde i Localstorage sÃ¥ bÃ¶rjar spelaren pÃ¥ 0.
        }
        this.toNum += 100
        this.toNum.toString()
        localStorage.setItem('localScore', this.toNum) // Nu Ã¤r vÃ¤rdet en strÃ¤ng igen sÃ¥ den kan sparas i Localstorage
        this.$store.state.playerData.show = false //HÃ¤r blir denna komponent osynlig igen
        this.startNewGame() // Spelet startas om ifall man spelar Mastermind, pÃ¥ de andra spelen finns en knapp dÃ¤r man vÃ¤ljer att starta nytt spel.
      }
    }
  }
</script>

